#page5 {
  width: 100%;
  background-color: $white;
  font-family: 'Open Sans', sans-serif;
  color: $black;

  #header5 {
    width: 100%;
    font-family: 'Raleway', sans-serif;
    text-align: center;
    padding: 8vh 0;

    h2 {
      font-size: 45px;
      font-weight: 600;
    }

    hr {
      display: block;
      margin: 2vh auto 0;
      width: 10%;
      height: 0.02vh;
      background-color: $red;
    }
  }

  #section5 {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    box-sizing: border-box;

    .section51 {
      width: calc(100% - 2px);
      height: 30vh;
      background-color: $grey;
      display: flex;
      flex-direction: column;
      align-items: center;

      .h25 {
        text-align: center;
        color: $black;
        padding: 23% 1.2vw 0;
      }

      .section511 {
        width: 100%;
        height: 70%;
        background-color: $darkgrey;
        text-align: center;
        padding-top: 19%;

        .h251 {
          color: $white;
          font-size: 20px;
        }

        .p51 {
          color: $white;
          font-size: 15px;
          margin-top: 1.2vh;
        }
      }

      .section512 {
        background-color: $lightgrey;
        width: 100%;
        height: 30%;
        display: flex;
        justify-content: flex-end;
        align-items: center;

        img {
          padding: 0 15px;
        }
      }
    }
  }

  #footer5 {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 40px 0;

    #button5 {
      width: 150px;
      height: 50px;
      background-color: $white;
      color: $red;
      border: 2px solid $red;

      &:hover {
        background-color: $red;
        color: $white;
        cursor: pointer;
      }

      a {
        text-decoration: none;
        color: inherit;
        font-family: 'Raleway', sans-serif;
        font-weight: 600;
        font-size: 16px;
        text-transform: uppercase;
      }
    }
  }

    #footer51 {
      width: 85%;
      margin: auto;
      padding: 7vh 0;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-wrap: wrap;

      .footer511 {
        width: 170px;
        height: 115px;
        border: 1px solid $lightgrey;
        margin: 1.2vh 1vw;
        position: relative;

        img {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
      }
    }
}

@media screen and (max-width: 990px) {
  #page5 {
    #section5 {
      grid-template-columns: 1fr 1fr;
    }
  }
}

@media screen and (max-width: 620px) {
  #page5 {
    #section5 {
      display: block;
    }
  }
}